/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */

 code[class*="language-"],
 pre[class*="language-"] {
   color: #ccc;
   background: none;
   font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
   text-align: left;
   white-space: pre;
   word-spacing: normal;
   word-break: normal;
   word-wrap: normal;
   line-height: 1.5;
   tab-size: 4;
 
   -webkit-hyphens: none;
   hyphens: none;
 
 }
 
 /* Code blocks */
 pre[class*="language-"] {
   padding: 1em;
   margin: .5em 0;
   overflow: auto;
 }
 
 :not(pre) > code[class*="language-"],
 pre[class*="language-"] {
   background: #2d2d2d;
 }
 
 /* Inline code */
 :not(pre) > code[class*="language-"] {
   padding: .1em;
   border-radius: .3em;
   white-space: normal;
 }
 
 .token.comment,
 .token.block-comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
   color: #999;
 }
 
 .token.punctuation {
   color: #ccc;
 }
 
 .token.tag,
 .token.attr-name,
 .token.namespace,
 .token.deleted {
   color: #e2777a;
 }
 
 .token.function-name {
   color: #6196cc;
 }
 
 .token.boolean,
 .token.number,
 .token.function {
   color: #f08d49;
 }
 
 .token.property,
 .token.class-name,
 .token.constant,
 .token.symbol {
   color: #f8c555;
 }
 
 .token.selector,
 .token.important,
 .token.atrule,
 .token.keyword,
 .token.builtin {
   color: #cc99cd;
 }
 
 .token.string,
 .token.char,
 .token.attr-value,
 .token.regex,
 .token.variable {
   color: #7ec699;
 }
 
 .token.operator,
 .token.entity,
 .token.url {
   color: #67cdcc;
 }
 
 .token.important,
 .token.bold {
   font-weight: bold;
 }
 .token.italic {
   font-style: italic;
 }
 
 .token.entity {
   cursor: help;
 }
 
 .token.inserted {
   color: green;
 }
 
 
*, *:before, *:after { 
  box-sizing          : border-box;
}

body {
  --bg-color          : #fcfcfc;
  --bg-secondary-color: #e5e5e5;
  --fonts-color       : #555555;
  --editor-color      : #4a4a4a;
  --key-color         : #cc2a36;
  
  background          : var(--bg-color);
  height              : 100%;
  width               : 100%;
  color               : var(--fonts-color);
  font-size           : 1rem;
  font-family         : 'Metrophobic', sans-serif;
  transition          : background 300ms, color 300ms;
}
*::selection {
  background          : var(--key-color);
  color               : var(--bg-secondary-color);
}
/* Header Styles */
#header {
  height            : 10vh;
  width             : 100%;
  margin            : 0 auto;
  padding           : 0 5vw;
  display           : flex;
  justify-content   : space-between;
  align-items       : center;
  border-bottom     : 1px solid var(--bg-secondary-color);
  background        : var(--bg-color);
  position          : fixed;
  top               : 0px;
  left              : 0px;
  z-index           : 1;
}

  #header h1 {
    color           : var(--key-color);
    display         : inline;
  }

  #header a,
  #header a:visited {
    color           : var(--fonts-color);
    text-decoration : none;
    transition      : color 300ms;
  }

  #header a:hover,
  #header a:active {
    color           : var(--key-color);
  }

  #header i.fab {
    color           : var(--key-color);
    font-size       : 2rem;
  }
#navbar {
  position            : fixed;
  top                 : 0;
  left                : 0;
  width               : 20vw;
  padding             : 15vh 0 1rem 5vw;
}

  #mob-menu-btn {
    display           : none;
  }

  #navbar #nav-container {
    border-right      : 1px solid var(--bg-secondary-color);
    position          : relative;
  }

    #nav-container header {
      color           : var(--key-color);
      padding-right   : 1rem;
    }

    #nav-container a.nav-link,
    #nav-container a.nav-link:visited {
      display         : block;
      color           : var(--fonts-color);
      text-decoration : none;
      padding         : 1rem 1rem 1rem 0;
      display         : flex;
      justify-content : space-between;
      align-items     : center;
      transition      : 300ms;
    }

      #nav-container a.nav-link:hover,
      #nav-container a.nav-link:active {
        color         : var(--key-color);
        background    : var(--bg-secondary-color);
        padding-left  : 1rem;
        border-left   : 5px solid var(--key-color);
      }

    #nav-container a.night-mode-toggler,
    #nav-container a.night-mode-toggler:visited {
      color           : var(--fonts-color);
      text-decoration : none;
      text-align      : center;
      font-size       : .8rem;
      display         : flex;
      justify-content : center;
      align-items     : center;
      padding-right   : 2rem;
      transition      : 300ms, border 100ms;
    }
    
      #nav-container a.night-mode-toggler:hover,
      #nav-container a.night-mode-toggler:active {
        color         : var(--key-color);
      }

    #nav-container a.night-mode-toggler i {
      color           : inherit;
      font-size       : 1.5rem;
    }

    #nav-container a i {
      font-size       : .8rem;
      padding-right   : .5rem;
      color           : var(--bg-secondary-color);
    }
#main-doc .main-section header {
  padding             : 8rem 0 .3rem 0;
}

  #main-doc .main-section header h1 {
    display           : inline;
  }

    #main-doc .main-section header h1::before {
      content         : "# ";
      vertical-align  : middle;
      color           : var(--bg-secondary-color);
    }

  #main-doc .main-section header a,
  #main-doc .main-section header a:visited {
    color             : var(--fonts-color);
    font-size         : 1.5rem;
    display           : inline;
    text-decoration   : none;
    transition        : color 300ms;
  }

  #main-doc .main-section header a:hover,
  #main-doc .main-section header a:active {
    text-decoration   : none;
    color             : var(--key-color);
  }
#footer {
  background        : var(--bg-secondary-color);
  height            : 3rem;
  width             : 100%;
  margin            : 0 auto;
  display           : flex;
  justify-content   : center;
  align-items       : center;
}

  #footer a,
  #footer a:visited {
    color           : var(--fonts-color);
    text-decoration : none;
    transition      : color 300ms;
    font-family     : 'Marmelad', sans-serif;
  }

  #footer a:hover,
  #footer a:active {
    color           : var(--key-color);
  }
  
    #footer i.fas {
      color         : var(--key-color);
    }

  #back-to-top {
    position        : fixed;
    bottom          : 1rem;
    right           : 1rem;
    font-size       : 3rem;
  }

    #back-to-top,
    #back-to-top:visited {
      color         : var(--key-color);
      transition    : color 300ms;
    }

    #back-to-top:hover,
    #back-to-top:active {
      color         : var(--fonts-color);
    }
@media ( max-width: 850px ) {

  #mob-menu-btn {
    display           : block;
  }

  #navbar {
    position          : fixed;
    padding           : 0;
    display           : flex;
    justify-content   : center;
    align-items       : center;
    top               : 0;
    left              : -50vw;
    background        : var(--bg-color);
    border-right      : 1px solid var(--bg-secondary-color);
    border-bottom     : 1px solid var(--bg-secondary-color);
    width             : 50vw;
    height            : 100%;
    min-height        : 100vh;
    z-index           : 1;
    transition        : -webkit-transform 300ms;
    transition        : transform 300ms;
    transition        : transform 300ms, -webkit-transform 300ms;
  }

    #navbar.show {
      -webkit-transform       : translateX( 50vw );
              transform       : translateX( 50vw );
    }

    #navbar #nav-container {
      border-right    : none;
    }

      #navbar #nav-container h3 {
        margin-top    : 0;
        padding: .5rem 1rem;
      }

      #navbar #nav-container a {
        padding: .5rem 1rem;
      }

  #main-content {
    padding           : 0 2rem;
    width             : 100%;
  }

  #main-doc {
    padding           : 6rem 1.2rem 0 0 !important;
    width             : 100% !important;
  }

    #main-doc header {
      padding           : 6rem 1.2rem 0 0 !important;
    }

    #main-doc h1 {
      text-align        : center !important;
      padding           : 0 0 .5rem 1.2rem;
    }

    #main-doc p {
      padding-left      : 2.5rem;
    }

    #main-doc hr {
      padding           : 0;
    }

    #main-doc .main-section .code-box,
    #main-doc .main-section .table,
    #main-doc .main-section figure {
      width             : 100vw !important;
      padding           : 0 !important;
      margin-left       : -2rem !important;
      padding           : 0 !important;
    }


    /* Remove FCC Testing Button on mobile */
    .fcc_test_ui {
      display: none !important;
    }

}
/* Base Styles */
a {
  outline         : 0;
}
h1, h2 {
  font-weight     : 100;
  font-size       : 1.6rem;
}

h3 {
  font-weight     : 100;
  font-size       : 1.2rem;
}

figure {
  margin          : 0;
  padding         : 0 0 0 1.5rem;
  width           : 100%;
  text-align      : center;
}

figure img {
  border          : 1px solid var(--bg-secondary-color);
  width           : 100%;
}

figure figcaption {
  margin          : 0;
  padding         : 0;
  text-align      : center;
}

hr {
  border          : 0; 
  height          : 1px; 
  text-align      : center;
  padding         : 0 5rem 0 1.5rem;
  background      : linear-gradient(to right, var(--bg-color), var(--bg-secondary-color), var(--bg-color));
  background      : -webkit-linear-gradient(left, var(--bg-color), var(--bg-secondary-color), var(--bg-color));
  filter          : progid:DXImageTransform.Microsoft.gradient( startColorstr=var(--bg-secondary-color), endColorstr=var(--bg-color),GradientType=1 );
}
  hr::after {
    content       : '\002756';
    display       : inline-block;
    position      : relative;
    top           : -15px;
    padding       : 0 10px;
    background    : var(--bg-color);
    color         : var(--bg-secondary-color);
    font-size     : 18px;
  }

p {
  padding-left    : 1.2rem;
  padding-bottom  : .5rem;
}

ul {
  border-left     : 5px solid var(--key-color);
  background      : var(--bg-color);
  list-style-type : none;
  margin          : 1.6rem;
  padding-left    : 1rem;
}

.table {
  padding-left    : 1.5rem;
  width           : 100%;
  overflow-x      : auto;
}

table {
  width           : 100%;
  border          : none;
  border-collapse : collapse;
}

  th {
    background    : var(--bg-secondary-color);
    color         : var(--fonts-color);
    padding       : .5rem 0 .5rem 1rem;
  }

  td {
    padding       : .5rem 0 .5rem 1rem;
    border-bottom : 1px solid var(--bg-secondary-color);
  }

:not(pre) > code[class*="language-"], pre[class*="language-"] {
  background: var(--editor-color);
}

#main-content {
  height          : 100%;
}

  #main-doc {
    padding       : 10rem 0 1rem 25vw;
    line-height   : 1.8rem;
    text-align    : justify;
  }

    #main-doc h1 {
      color       : var(--key-color);
      text-align  : left;
    }

      #main-doc h1 small {
        color       : var(--fonts-color);
      }

    #main-doc .main-section {
      padding     : 0 5vw 3rem 0;
    }

      #main-doc .main-section a,
      #main-doc .main-section a:visited {
        color           : var(--key-color);
        text-decoration : none;
        transition      : color 300ms;
      }

      #main-doc .main-section a:hover,
      #main-doc .main-section a:active {
        text-decoration : underline;
      }


      #main-doc .main-section .code-box {
        padding-left    : 1.2rem;
        width           : 100%;
      }
      
      #main-doc .main-section pre {
        position        : relative;
        width           : 100%;
        overflow        : auto;
      }
      
      #main-doc .main-section .code-box code {
        font-family     : 'Share Tech Mono', monospace;
        font-size       : 1rem;
      }


/* Styling the FCC Testing Button */
.fcc_hamburger {
  background: var(--bg-secondary-color) !important;
}
